//引入仓库，状态机
import { createNamespacedHelpers } from 'vuex';
const { mapActions, mapState, mapMutations } = createNamespacedHelpers('pet');

import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue';

export default {
	data() {
		return {
			flag: false, //判断是否全选
			money: 0, //总金额
			num: 0 //删除数据后，用来判断是否全选
		};
	},
	computed: {
		...mapState(['list'])
	},
	mounted() {
		//进来渲染购物车页面
		this.getShopAartById();
	},
	onShow() {
		this.getShopAartById();
	},
	methods: {
		...mapActions(['getShopAartById', 'updateCount']),

		all(index) {
			//全选
			this.flag = !this.flag;
			if (this.flag) {
				for (var i = 0; i < this.list.length; i++) {
					this.list[i].check = true;
				}
				this.num = this.list.length;
				this.total();
			} else {
				for (var i = 0; i < this.list.length; i++) {
					this.list[i].check = false;
				}
				this.num = 0;
				this.money = 0;
			}
			this.$emit('getShopAartById', this.list);
		},
		signCount(id) {
			//减少商品
			for (var i = 0; i < this.list.length; i++) {
				var obj = this.list[i];
				if (obj._id == id) {
					if (obj.count - 0 > 1) {
						~~obj.count--;
						console.log(id, obj.count);
						this.updateCount({ _id: id, count: obj.count });
					}
					break;
				}
			}
			this.total();
		},
		addCount(id) {
			//增加商品
			for (var i = 0; i < this.list.length; i++) {
				var obj = this.list[i];
				if (obj._id == id) {
					if (obj.count - 0 < obj.stock - 0) {
						~~obj.count++;
						this.updateCount({ _id: id, count: obj.count });
						// this.$emit('getShopAartById', this.list);
					} else {
						uni.showToast({
							title: '当前库存不足'
						});
					}
					break;
				}
			}
			this.total();
		},
		inp(index) {
			//商品选择
			for (var i = 0; i < this.list.length; i++) {
				if (this.list[i]._id == index) {
					this.list[i].check = !this.list[i].check;
					if (this.list[i].check == false) {
						//如果有条数据没选择，就取消全选
						this.flag = false;
						this.num -= 1;
						this.money -= this.list[i].price * this.list[i].count;
						this.$emit('getShopAartById', this.list);
					} else {
						this.num += 1;
						this.money += this.list[i].price * this.list[i].count;
						if (this.num == this.list.length) {
							//如果全部选中了
							this.flag = true;
						}
						this.$emit('getShopAartById', this.list);
					}
				}
			}
		},
		removeM(id) {
			//商品删除
			var _this = this;
			for (var i = 0; i < this.list.length; i++) {
				var obj = this.list[i];
				if (obj._id == id) {
					uni.showModal({
						title: '',
						content: '确定删除,不再看看吗',
						confirmText: '确定',
						success: function(res) {
							if (res.confirm) {
								_this.list.splice(i, 1);
								_this.$emit('getShopAartById', _this.list);
								if (obj.check) {
									//如果选中状态下删除
									_this.num--;
									_this.total();
								}
								if (_this.num == 0) {
									//删除后还需要判断下当前的num是否跟当前的数据长度相同，是否显示全选
									_this.flag = false;
								} else {
									if (_this.num == _this.list.length) {
										_this.flag = true;
									}
								}
							}
						}
					});
					break;
				}
			}
		},
		total() {
			//计算总价---只计算选中的；
			this.money = 0;
			for (var i = 0; i < this.list.length; i++) {
				if (this.list[i].check == true) {
					this.money += this.list[i].price * this.list[i].count;
				}
			}
		},
		goPay() {
			//去结算
			const arry = this.list.filter(function(item) {
				//结算过虐选中的数据，arry这个数组就是最后你要提交的数据
				return item.check === true;
			});
			//点击去结算的时候，讲所选的数据保存在本地
			try {
			    uni.setStorageSync('arry', arry);
			} catch (e) {
			    // error
			}
			// console.log('arry: ' + JSON.stringify(arry));
			
			//点击结算跳到订单结算页面
			uni.navigateTo({
				url: '/pages/PaymentOfAnOrder/PaymentOfAnOrder'
			});
			
			if (arry.length == 0) {
				uni.showToast({
					title: '您还没有选中要结算的商品',
					icon: 'none'
				});
				return;
			}
		}
	},
	components: {
		uniSwipeAction
	}
};
